import { createContext } from 'react';

import { EventContext } from '@enactive-ac/api/src/domain/EventContext';
import {
  FindToursRequest,
  FindToursResponse,
} from '@enactive-ac/api/src/domain/FindTours';
import { PostTourActionRequest } from '@enactive-ac/api/src/domain/PostTourAction';
import { Tour } from '@enactive-ac/api/src/domain/Tour';
import { TourStepButton } from '@enactive-ac/api/src/domain/TourStepButton';
import { DefaultTheme } from '@mui/styles';
import { AxiosInstance } from 'axios';

import { RecursivePartial } from '../util/RecursivePartial';
import { deepMerge } from '../util/deepMerge';

export class TourService {
  constructor(private readonly axios: AxiosInstance) {}

  public async getTour(
    eventContext: RecursivePartial<EventContext> | undefined,
  ): Promise<{ theme: DefaultTheme; tour: Tour | undefined }> {
    const req: FindToursRequest = {
      context: this.resolveEventContext(eventContext),
    };
    const response = await this.axios.post<FindToursResponse>('tours', req);
    const theme: DefaultTheme = {
      palette: {
        primary: {
          contrastText: response.data.site.theme?.primaryContrastText ?? '#fff',
          main: response.data.site.theme?.primaryColor ?? '#4f4f4f',
        },
      },
    };
    return { theme, tour: response.data.tours[0] };
  }

  public async collectTourAction(
    eventContext: RecursivePartial<EventContext> | undefined,
    tourId: string,
    stepId: string,
    action: TourStepButton['action'],
  ): Promise<void> {
    const context = this.resolveEventContext(eventContext);
    const req: PostTourActionRequest = { context };
    await this.axios.post(
      `tours/${tourId}/steps/${stepId}/actions/${action}`,
      req,
    );
  }

  private resolveEventContext(
    context: RecursivePartial<EventContext> | undefined,
  ): EventContext {
    return deepMerge(
      {
        device: {
          locale:
            document.querySelector('html')?.getAttribute('lang') ?? undefined,
        },
        page: {
          url: window.location.href,
        },
        user: {
          anonymous: true,
          id: '',
        },
      },
      context ?? {},
    );
  }
}

export const TourServiceContext = createContext<TourService>(
  null as unknown as TourService,
);
